.menu__link {
    color: #697CA6;
    font-family: Quicksand;
    font-size: 22px;
    font-weight: 500;
    line-height: 2.5;
    letter-spacing: 0.05em;
    text-align: center;
    text-shadow: 0px 0px 5px #E6E6E6;
}

